@import 'styles/variables.scss';

.login-page {
  margin: 0px;
  width: 100%;
  margin-left: auto;
  margin-right: auto;
}

.login-page-content {
  width: 100%;
  height: 100vh;
  background-repeat: no-repeat;
  background-position: right top;
  background-size: auto 93%;
  overflow-y: auto;
  box-sizing: border-box;
  background-color: $theme-dark;
  padding-bottom: 8vh;
}

@media only screen and (max-width: 1024px) {
  .login-page-content {
    background-image: none;
  }
}

.login-form {
  padding: 0 1.5em;
  color: $off-white;
}

@media only screen and (max-width: 1024px) {
  .login-form-loader {
    width: 100%;
    align-items: center;
    flex-direction: column;
    justify-content: center;
    display: flex;
  }
}

.login-form-input-header {
  color: $off-white;
  font-size: 36px;
  margin-bottom: 1rem;
}

.login-form-input-error {
  color: $theme-error;
  font-size: 1rem;
}

.login-form-container {
  display: block;
  margin-top: 30px; /*modal distance from top of the screen*/
  box-sizing: border-box;
  width: 50%;
}

@media only screen and (max-width: 1024px) {
  .login-form-container {
    text-align: center;
    width: 100%;
  }
}

.login-form-input {
  font-size: 1.5em;
  line-height: normal;
  padding: 0.25rem 0.1rem 0px 0.1rem;
  border: 0px;
  background-color: $theme-dark;
  border-top: 0px;
  border-left: 0px;
  border-right: 0px;
  border-bottom: 2px solid $off-white;
  -webkit-appearance: none;
  -moz-appearance: none;
  appearance: none;
  outline: 0;
  width: 50%;
  min-width: 300px;
  max-width: 500px;
  margin: 0 2px 1rem; /*last value controls how far the next input starts*/
  font-variant-ligatures: none;
  -webkit-transition: box-shadow 70ms ease-out, border-color 70ms ease-out;
  -moz-transition: box-shadow 70ms ease-out, border-color 70ms ease-out;
  transition: box-shadow 70ms ease-out, border-color 70ms ease-out;
  box-shadow: none;
  color: $off-white;
  height: auto;
  display: inline-block;
}

.login-form-input:disabled {
  color: #686868;
}

.login-modal ::-webkit-input-placeholder {
  /* Chrome/Opera/Safari */ /*changes to the placeholder text*/
  color: #c0e2fd;
}

.login-form-button {
  background-color: $theme-light;
  border-color: $theme-light;
  text-align: left;
}

.login-form-button:hover, .login-form-button:active {
  color: $theme-light !important;
  border-color: $off-white !important;
  background-color: $off-white !important;
}

.login-form-link {
  color: inherit;
  text-decoration: none;
  font-size: 1.2rem;
}

.login-form-link:hover {
  color: inherit;
  text-decoration: underline;
}
